#about h1 {
    font-family: 'Dosis',sans-serif;
    padding-bottom:4rem;
    font-weight: 320;
    font-size:2rem;
}

#about {
    margin-top:6rem;
    text-align:center;
    display:grid;
    width:80%;
    margin: 0 auto;   
}


.team_page{
    background-color: #5B43EF;
    color:white;
}

#intro {
    margin:0 auto;
    width:100%;
    text-align:left;
}

#intro h1 {
    margin:3rem auto;
    width:80%;
    font-weight: 320;
}

#intro p {
    margin:5rem auto;
    font-family:'Dosis',sans-serif;
    font-size:1.1rem;
    width:75%;
    font-weight: 300;
    text-align: left;
}

#about h2 {
    margin-top:2rem;
    font-family:'Dosis',sans-serif;
    font-size:1.7rem;
    font-weight: 400;
}

#about h3 {
    margin-top:1rem;
    font-size:1rem;
    font-weight:300;
}

