    font-size:0.9rem;
    font-weight:50;
    width:80%;
    margin: 1rem auto;
    margin-bottom:6rem;
    letter-spacing: 1px;
    line-height:25px;
}

#about #row1 {
    margin:0 auto;
    display:grid;
}

#about #row2 {
    margin:0 auto;
    display:grid;
}

